#gridMain,
#gridMain table td,
#gridMain table th{font-size: 16px !important;}
#gridMain { display: flex;  flex-wrap: nowrap; width: 80vw  }
#gridMain > div { height: 80vh;}
#map { min-width: 80vh; }

#RightDiv > div > div { height: 30vh !important; }

#moveBtnDiv button,
#saveBtnDiv button{ margin: 5px; }
#moveBtnDiv button img,
#saveBtnDiv button img{ width: 20px; height: 20px; padding: 5px; }

#infoButton { align-self: end; border: black 1px dashed; margin: 5px }
#infoButton span{ font-size: initial; padding: 5px; padding-right: 0 }
#infoButton img{ width: 20px; height: 20px; padding: 5px }

#tankDiv img { width: 100px; height: 100px; }

#commandsDiv { height: 20vw; min-width: 300px}

#commands th{ position: sticky; top: 0;}
#commands td,
#commands th{ text-align: center; background: lightgray; border:  2px solid black  }

#commands tr > td:nth-child(1){ width: 20px}

#gameButtonsDiv{ min-width: 232px; }

.gameButtonsColumn { min-width: 120px; }


.gameTD{
    margin: 2px;
    width: 50px;
    height: 50px;
}

#gameButtons { display: flex; flex-wrap: wrap; justify-content:space-evenly }

.driver.selected{ border: #F00 solid !important; }
.gunner.selected{ border: #0F0 solid !important; }
.charger.selected{ border: #00F solid !important; }

.flex-column{ display: flex; flex-direction: column; align-items: center}
.flex-row{ display: flex; flex-direction: row; align-items: center}
.flex-justify-start {justify-content: flex-start;}
.flex-justify-center {justify-content: center;}
.flex-justify-evenly {justify-content:space-evenly;}
.flex-justify-between {justify-content:space-between;}
.flex-align-start {align-items: flex-start;}
.flex-wrap {flex-wrap: wrap}

.padding-1 {padding: 1px;}
.padding-2 {padding: 2px;}
.padding-5 {padding: 5px;}
.padding-10 {padding: 10px;}
.margin-1 {margin: 1px;}
.margin-2 {margin: 2px;}
.margin-5 {margin: 5px;}
.margin-10 {margin: 10px;}

.widthAll { width: 100%; }
.overflow { overflow: auto}
.overflow-visible { overflow: scroll}
.overflow-x-visible { overflow-x: scroll}
.overflow-y-visible { overflow-y: scroll}